@media (min-width: 320px) {
	.header {
		width: 100%;
		height: 50px;
		z-index: 1;
		background: #222;
		display: flex;
		align-items: center;
		justify-content: center;
		color: #e1e3e6;
		font-size: 0.7rem;
		border-bottom: #272727 solid 0.1px;
		padding-right: 8px;
	}
	.container {
		max-width: 800px;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: 8px 0px 8px 0;
	}
	.titleWrapper {
		display: flex;
		flex-direction: row;
		align-items: center;
		width: 165px;
		height: 100%;
	}
	.titleImg {
		width: 20px;
		aspect-ratio: 1;
		margin-right: 8px;
	}
	.title {
		font-weight: 500;
	}
	.inputWrapper {
		position: relative;
		width: 100%;
		height: 100%;
	}
	.searchSvg {
		position: absolute;
		left: 10px;
		top: 50%;
		transform: translateY(-50%);
		height: 60%;
		aspect-ratio: 1;
	}
	.searchUse {
		filter: invert(1);
		opacity: 0.4;
	}
	.input {
		width: 100%;
		height: 100%;
		background: #464646;
		border-radius: 10px;
		color: #ccc;
		padding-left: 40px;
		transition: 0.15s;
		&:hover,
		&:focus {
			background: #4a4a4a;
		}
	}
}
